.womenListContainer {
    width: 100vw;
    margin: 0;
    padding: 0;
}

.women-category-container {
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    background-image: url("https://cdn.pixabay.com/photo/2020/09/15/15/56/background-5574027_1280.png");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    position: relative;
}

.womenCategory-h1 {
    color: #727272;
    background:
    linear-gradient(270deg, #727272, transparent);
}

.women-category-list {
    width: 600px;
    height: 700px;
    margin-left: 30%;
    margin-top: 5%;
}

.womenCategory-imageModel-shoe {
    width: 300px;
    height: 400px;
    object-fit: cover;
    border-radius: 30px;
    border: 5px solid #727272;
    margin-bottom: 200px;
}

.womenCategory-imageModel-accessory {
    width: 300px;
    height: 400px;
    object-fit: cover;
    border-radius: 30px;
    border: 5px solid #727272;
    margin-top: 50px
}

.women-h1 {
    background:
        linear-gradient(270deg, rgba(123, 59, 3, 0.594), transparent);
    position: sticky;
    top: 0;
}

.men-word-in-women-page {
    font-family: 'Archivo Black', sans-serif;
    font-size: 30px;
    position: relative;
    width: 80px;
    height: 100%;
    padding: 18px 0 0 0;
    color: azure;
    cursor: default;
}

.women-word-in-women-page {
    font-family: 'Archivo Black', sans-serif;
    font-size: 30px;
    position: relative;
    width: 140px;
    height: 100%;
    padding: 18px 0 0 0;
    color: azure;
    cursor: default;
    border-right: 10px solid black;
}